import React from 'react';
import FeedPostCard from "./FeedPostCard";
import {FeedPost} from "../../FeedContext";
import {PhotoFeedPost} from "../CreatePostPage";

interface FeedProp {
    posts: (FeedPost|PhotoFeedPost)[]
}

function Feed({posts}: FeedProp) {
    return <div className="d-flex flex-column align-items-center">
        {posts.map((post, index) => <FeedPostCard key={`feed-post-${index}`} post={post}/>)}
    </div>
}

export default Feed;