import React from 'react';
import {FeedPost} from "../../FeedContext";
import {PhotoFeedPost} from "../CreatePostPage";

interface FeedPostCardProp {
    post: FeedPost | PhotoFeedPost,
}

function FeedPostCard({post}: FeedPostCardProp) {
    let status
    const isPhoto = post.post_type === 'photo_set'
    let url = ''
    let alt = ''
    if (isPhoto) {
        status = (post as PhotoFeedPost).photo_description
        url = (post as PhotoFeedPost).photo_files[0].text
        alt = (post as PhotoFeedPost).photo_files[0].text
    } else {
        status = (post as FeedPost).user_status
    }
    return <div className="mb-3 w-100">
        <div className="card">
            {isPhoto ? <img src={url} className="card-img-top" alt={alt}/> : null}
            <div className="card-body" dangerouslySetInnerHTML={{__html: status}}>
                {/*{post.user_status}*/}
            </div>
        </div>
    </div>
}

export default FeedPostCard;