import React, {createContext, useState, ReactNode} from 'react';

interface AuthTokenContextType {
    token: string;
    setAuthToken: (message: string) => void;
}

const AuthTokenContext = createContext<AuthTokenContextType>({
    token: '',
    setAuthToken: () => {
    },
});

interface AuthTokenProviderProps {
    children: ReactNode;
}

const AuthTokenProvider: React.FC<AuthTokenProviderProps> = ({children}) => {
    const tokenStr = window.localStorage.getItem(`${process.env.REACT_APP_AUTH_TOKEN_ID}`)

    const [token, setAuthToken] = useState<string>(tokenStr || '');

    return (
        <AuthTokenContext.Provider value={{token, setAuthToken}}>
            {children}
        </AuthTokenContext.Provider>
    );
};

export {AuthTokenContext, AuthTokenProvider};
