import axios from "axios";

export const getAuthToken = () => {
    const tokenDataStr = window.localStorage.getItem(`${process.env.REACT_APP_AUTH_TOKEN_ID}`)
    const tokenData = JSON.parse(tokenDataStr || '{}')
    return `${tokenData.token_type} ${tokenData.access_token}`
}

export const getPostTypes = async () => {
    return await get(`${process.env.REACT_APP_GET_POST_TYPES}`)
}

export const post = async (endpoint: string, data: Object, contentType = 'application/json') => {
    const customHeaders = {
        'Authorization': getAuthToken(),
        'Content-Type': contentType,
    };

    const response = await axios.post(endpoint, data, {headers: customHeaders});

    return response.data
}

export const get = async (endpoint: string) => {
    const customHeaders = {
        'Authorization': getAuthToken(),
        'Content-Type': 'application/json',
    };

    const response = await axios.get(endpoint, {headers: customHeaders});

    return response.data
}