import React, { createContext, useState, ReactNode } from 'react';

export enum FlashMessageType {
    Success = 'success',
    Error = 'error'
}

export interface FlashMessage {
    type: FlashMessageType,
    message: string
}

interface FlashMessageContextType {
    message: FlashMessage;
    setMessage: (message: FlashMessage) => void;
}

const MessageContext = createContext<FlashMessageContextType>({
    message: {type:FlashMessageType.Success, message: ""},
    setMessage: () => {},
});

interface FlashMessageProviderProps {
    children: ReactNode;
}

const FlashMessageProvider: React.FC<FlashMessageProviderProps> = ({ children }) => {
    const [message, setMessage] = useState<FlashMessage>({type:FlashMessageType.Success, message: ""});

    return (
        <MessageContext.Provider value={{ message, setMessage }}>
            {children}
        </MessageContext.Provider>
    );
};

export { MessageContext, FlashMessageProvider };
