import React, {useContext, useState} from 'react';
import {NavLink, useNavigate} from "react-router-dom";
import {BsFillArrowLeftCircleFill} from "react-icons/bs";
import {FlashMessageType, MessageContext} from "../MessageContext";
import toast from "react-hot-toast";
import {FeedContext, FeedPost} from "../FeedContext";
import {formatBiography} from "./partials/PostCard";
import {post} from "../lib/lib";
import PhotoPreviewer from "../partials/components/PhotoPreviewer";

export interface PhotoFeedPost {
    post_type: string,
    "privacy": number,
    photo_files: [
        {
            "id": number,
            "type": string,
            "status": string,
            "text": string,
            "tagged_friends": number[]
        }
    ],
    "photo_description": string
}

interface PhotoUploadResult {
    original_name: string,
    item_type: string,
    user_id: number,
    user_type: string,
    width: number,
    height: number,
    temp_file: number,
    url: string
}

interface FormData {
    post_type: string,
    tagged_friends: (number | null)[],
    category: string;
    name: string;
    biography: string;
    impact: string;
    notify_nominee: string;
    add_project: string;
    project: string;
    user_status: string;
    privacy: number;
}

interface ProjectTextAreaPros {
    addProject: string,
    onChange: (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>) => void
}

function ProjectTextArea({addProject, onChange}: ProjectTextAreaPros) {
    if (addProject === 'yes') {
        return <div className="mb-3">
            <label htmlFor="project" className="form-label">Project</label>
            <textarea className="form-control" id="project" name="project" onChange={onChange}></textarea>
        </div>
    }

    console.log("addProject", addProject)

    return null
}

function CreatePostPage() {
    const [selectedFile, setSelectedFile] = useState<File | null>(null);

    const {setMessage} = useContext(MessageContext);
    const {feed, setFeed} = useContext(FeedContext);

    // useEffect(() => {
    //     async function allPostTypes() {
    //         const types = await getPostTypes()
    //         console.log(types)
    //         setPostTypes(types)
    //     }
    //
    //     if (!postTypes) {
    //         allPostTypes()
    //     }
    // }, [])

    // console.log('post types', postTypes)

    const [formData, setFormData] = useState<FormData>({
        post_type: 'activity_post',
        tagged_friends: [null],
        category: 'Academic leader',
        name: '',
        biography: '',
        impact: '',
        notify_nominee: 'no',
        add_project: 'no',
        project: '',
        user_status: '',
        privacy: 4,
    });

    console.log('di')
    const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>): void => {
        console.log('handling change', e.target)
        const {name, value} = e.target;
        setFormData({...formData, [name]: value});
    };

    const navigate = useNavigate();

    const validateForm = () => {
        if (!formData.name) {
            toast.error("Name field is required")
            console.log('name invalid')
            return false
        }

        if (!formData.impact) {
            toast.error("Impact field is required")
            console.log('Impact invalid')
            return false
        }

        if (!formData.biography) {
            toast.error("Biography field is required")
            console.log('Biography invalid')
            return false
        }

        if (formData.add_project === 'yes' && !formData.project.trim()) {
            toast.error("Project field is required when add project is 'yes'.")
            console.log('Project invalid')
            return false
        }

        return true
    }

    const submitPhoto = async (): Promise<PhotoUploadResult | null> => {
        if (selectedFile) {
            const payload = new FormData()
            payload.append("file", selectedFile)
            payload.append("name", "file")
            payload.append("type", "photo")
            payload.append("item_type", "photo")
            payload.append("file_type", "photo")

            const response = await post(`${process.env.REACT_APP_UPLOAD_PHOTO_URL}`, payload, 'multipart/form-data')

            return response.data
        }
        return null
    }

    const submitPost = async (payload: FeedPost | PhotoFeedPost) => {
        const response = await post(`${process.env.REACT_APP_CREATE_FEED_URL}`, payload)

        return response.data
    }

    const formatUserStatus = () => {
        const project = formData.add_project === 'yes' ? `<p class="card-text"><span class="fw-bold">Project:</span> ${formData.project}</p>` : ''
        return `<h5 class="card-title"><strong>${formData.name}</strong></h5>
                <p class="card-text"><span class="fw-bold">Category:</span> ${formData.category}</p>
                <p class="card-text"><span class="fw-bold">Impact:</span> ${formData.impact}</p>
                <p class="card-text"><span class="fw-bold">Biography:</span> ${formatBiography(formData.biography)}</p>
                ${project}`
    }

    const getPayload = (photoResult: PhotoUploadResult | null) => {
        console.log("photoResult", photoResult)
        if (photoResult) {
            return {
                "post_type": "photo_set",
                "privacy": formData.privacy,
                "photo_files": [
                    {
                        "id": photoResult.temp_file,
                        "type": photoResult.item_type,
                        "status": "new",
                        "text": "",
                        "tagged_friends": []
                    }
                ],
                "photo_description": formatUserStatus(),
            } as PhotoFeedPost
        }

        return {
            user_status: formatUserStatus(),
            privacy: formData.privacy,
            post_type: formData.post_type
        } as FeedPost
    }

    const handleSubmit = async (e: React.FormEvent<HTMLFormElement>): Promise<void> => {
        e.preventDefault();
        if (!validateForm()) {
            console.log('invalid', toast)

            return
        }

        try {
            const photoResult = await submitPhoto()
            const payload: FeedPost | PhotoFeedPost = getPayload(photoResult)
            console.log('Submitting:', payload);
            const response = await submitPost(payload)
            console.log('API Response:', response)

            setMessage({message: 'Post created successfully', type: FlashMessageType.Success})

            if (payload.post_type === 'photo_set') {
                (payload as PhotoFeedPost).photo_files[0].text = photoResult?.url || ''
            }

            const newFeed = [payload].concat(feed)
            setFeed(newFeed)
            window.localStorage.setItem('feed', JSON.stringify(newFeed))
            navigate('/')
        } catch (error) {
            console.error('Form submission failed:', error);
        }
    };

    return <>
        <div className="pb-3">
            <NavLink to="/" className="rounded-pill text-decoration-none">
                <BsFillArrowLeftCircleFill/> <span className="text-black">Honours/Gratitude</span>
            </NavLink>
        </div>
        <form onSubmit={handleSubmit}>
            <div className="mb-3">
                <label htmlFor="category" className="form-label">Privacy</label>
                <select className="form-select" value="4" aria-label="Default select example"
                        name="privacy"
                        onChange={handleChange}>
                    <option value="0">Public</option>
                    <option value="1">Community</option>
                    <option value="2">Friends</option>
                    <option value="3">Friends of Friends</option>
                    <option value="4">Only Me</option>
                </select>
            </div>
            <div className="mb-3">
                <label htmlFor="category" className="form-label">Category</label>
                <select className="form-select" value="Academic leader" aria-label="Default select example"
                        name="category"
                        onChange={handleChange}>
                    {/*<option value="">Categories</option>*/}
                    {/*<option value="post">Post</option>*/}
                    {/*<option value="poll">Poll</option>*/}
                    {/*<option value="blog">Blog</option>*/}
                    {/*<option value="photo_set">Photo set</option>*/}
                    {/*<option value="friend">Friend</option>*/}
                    {/*<option value="user">User</option>*/}
                    <option value="Academic leader">Academic leader</option>
                </select>
            </div>
            <div className="mb-3">
                <label htmlFor="name" className="form-label">Name</label>
                <input type="text" className="form-control" id="name" name="name" aria-describedby="emailHelp"
                       required onChange={handleChange}/>
                {/*<div id="emailHelp" className="form-text">We'll never share your email with anyone else.</div>*/}
            </div>
            <div className="mb-3">
                <label htmlFor="biography" className="form-label">Nominee's biography</label>
                <textarea className="form-control" id="biography" name="biography" onChange={handleChange}
                          required></textarea>
            </div>
            <div className="mb-3">
                <label htmlFor="impact" className="form-label">Nominee's impact</label>
                <textarea className="form-control" id="impact" name="impact" onChange={handleChange}
                          required></textarea>
            </div>
            {/*<div className="row mb-3">*/}
            {/*    <div className="col">Notify nominee:</div>*/}
            {/*    <div className="col form-check form-check-inline">*/}
            {/*        <input className="form-check-input" type="radio" name="notify_nominee" id="notify-nominee"*/}
            {/*               value="yes" onChange={handleChange}/>*/}
            {/*        <label className="form-check-label" htmlFor="notify-nominee">Yes</label>*/}
            {/*    </div>*/}
            {/*    <div className="col form-check form-check-inline">*/}
            {/*        <input className="form-check-input" type="radio" name="notify_nominee" id="do-not-notify-nominee"*/}
            {/*               value="no" onChange={handleChange}/>*/}
            {/*        <label className="form-check-label" htmlFor="do-not-notify-nominee">No</label>*/}
            {/*    </div>*/}
            {/*</div>*/}
            <div className="row mb-3">
                <div className="col">Add project:</div>
                <div className="col form-check form-check-inline">
                    <input className="form-check-input" type="radio" name="add_project" id="add-project"
                           value="yes" onChange={handleChange}/>
                    <label className="form-check-label" htmlFor="add-project">Yes</label>
                </div>
                <div className="col form-check form-check-inline">
                    <input className="form-check-input" type="radio" name="add_project" id="do-not-add-project"
                           value="no" onChange={handleChange}/>
                    <label className="form-check-label" htmlFor="do-not-add-project">No</label>
                </div>
            </div>

            <ProjectTextArea addProject={formData.add_project} onChange={handleChange}/>

            <PhotoPreviewer setSelectedFile={setSelectedFile}/>
            {/*<div className="mb-3 form-check">*/}
            {/*    <input type="checkbox" className="form-check-input" id="exampleCheck1"/>*/}
            {/*    <label className="form-check-label" htmlFor="exampleCheck1">Notify nominee</label>*/}
            {/*</div>*/}

            <button type="submit" className="btn btn-primary">Post</button>
        </form>
    </>;
}

export default CreatePostPage;