import './App.css';
import FeedPage from "./feed/FeedPage";
import Header from "./partials/components/Header";
import {BsFillHouseDoorFill} from "react-icons/bs"
import {NavLink, Route, BrowserRouter as Router, Routes} from "react-router-dom";
import CreatePostPage from "./feed/CreatePostPage";
import {MessageContext, FlashMessageProvider, FlashMessageType} from "./MessageContext";
import {useContext} from "react";
import toast, {Toaster} from "react-hot-toast";
import {FeedProvider} from "./FeedContext";
import {AuthTokenProvider} from "./AuthTokenContext";
import ProtectedRoute from "./ProtectedRoute";
import SignIn from "./auth/SignIn";

function App() {
    const {message, setMessage} = useContext(MessageContext);

    if (message.message) {
        console.log('Toasting:', message.message)

        toast[message.type](message.message, {
            position: "bottom-right",
        })

        setMessage({message: '', type: FlashMessageType.Success})
    }

    return (
        <Router>
            <AuthTokenProvider>
                <FlashMessageProvider>
                    <FeedProvider>
                        <Toaster/>
                        <div className="container-fluid bg-light p-0">
                            <Header/>

                            {/*Main*/}
                            <div className="row">
                                {/*Left*/}
                                <div className="vh-100 col-md-3">
                                    <div className="bg-white px-4 pt-5 vh-100 me-4">
                                        <ul className="list-group list-group-flush">
                                            <NavLink to="/"
                                                     className="list-group-item rounded-2 border-0 list-group-item-action">
                                    <span
                                        className="rounded-circle bg-transparent p-2 me-2"><BsFillHouseDoorFill/></span> Home
                                            </NavLink>
                                            <NavLink to="/post"
                                                     className="list-group-item rounded-2 border-0 list-group-item-action">
                                                Create post
                                            </NavLink>
                                        </ul>
                                    </div>
                                </div>

                                {/*Middle*/}
                                <div className="vh-100 col-7 px-4 pt-3 pb-8 ">
                                    <div className="bg-white p-5 rounded-3">
                                        <Routes>
                                            <Route path="/signin" element={<SignIn />}/>
                                            <Route path="/" element={<ProtectedRoute children={<FeedPage/>} />}/>
                                            <Route path="/post" element={<ProtectedRoute children={<CreatePostPage/>} />}/>
                                        </Routes>
                                    </div>
                                </div>

                                {/*Right*/}
                                <div className="vh-100 col"></div>
                            </div>
                        </div>
                    </FeedProvider>
                </FlashMessageProvider>
            </AuthTokenProvider>
        </Router>
    )
}

export default App;
