import React, { createContext, useState, ReactNode } from 'react';
import {PhotoFeedPost} from "./feed/CreatePostPage";

export interface FeedPost {
    user_status: string,
    privacy: number,
    post_type: string,
}

interface FeedContextType {
    feed: (FeedPost|PhotoFeedPost)[];
    setFeed: (posts: (FeedPost|PhotoFeedPost)[]) => void;
}

const FeedContext = createContext<FeedContextType>({
    feed: [],
    setFeed: () => {},
});

interface FeedProviderProps {
    children: ReactNode;
}

const FeedProvider: React.FC<FeedProviderProps> = ({ children }) => {
    const allFeed = JSON.parse(window.localStorage.getItem('feed') || '[]').slice(0, 100)
    console.log("feed", allFeed)
    const [feed, setFeed] = useState<(FeedPost|PhotoFeedPost)[]>(allFeed);

    return (
        <FeedContext.Provider value={{ feed, setFeed }}>
            {children}
        </FeedContext.Provider>
    );
};

export { FeedContext, FeedProvider };
